html {
    font-size: 62.5%;
}

body {
    margin: 0;
    font-family: 'IBMPlexSans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', FrutigerNext, FrutigerNextBold,
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-overflow-scrolling: touch;
}

code {
    font-family: IBMPlexSans, source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
    font-family: FrutigerNext;
    src: local(FrutigerNext), url('components/common/fonts/Frutiger Next.ttf') format('truetype');
}

@font-face {
    font-family: FrutigerNextBold;
    src: local(FrutigerNext-Bd), url('components/common/fonts/Frutiger Next Bold.ttf') format('truetype');
}